.card {
    background: #7fb285;
    color: white;
    padding: 2em;
    border-radius: 30px;
    width: 70%;
    max-width: 420px;
    display: flex;
    align-items: center;
    justify-content:center;
    flex-direction: column;
   
  }

  
  .button {


    height: 50px;
    border-radius: 5px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 20px;
    background-color: #2c4251;
    color: white;
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 25px;
  }
  

